// Core
import React, { Component } from 'react';
import moment from 'moment';

// Instruments
import { getDisplayName } from './helpers';
import { url, token } from 'config/api';

export const withState = (Enhanceable) => {
    class WithState extends Component {
        static getDerivedStateFromProps () {
            const tasks = JSON.parse(localStorage.getItem('tasks'));

            return tasks ? { tasks } : null;
        }

        state = {
            newTaskMessage:  '',
            tasksFilter:     '',
            isTasksFetching: false,
            tasks:           [],
        };

        componentDidMount () {
            this._fetchTasks();
        }

        componentDidUpdate () {
            localStorage.setItem('tasks', JSON.stringify(this.state.tasks));
        }

        _fetchTasks = async () => {
            try {
                this._setTasksFetchingState(true);

                const response = await fetch(url, {
                    method:  'GET',
                    headers: {
                        Authorization: token,
                    },
                });

                const { data: tasks, message } = await response.json();

                if (response.status !== 200) {
                    throw new Error(message);
                }

                this.setState({
                    tasks:           this._sortTasksByGroup(tasks),
                    isTasksFetching: false,
                });
            } catch (error) {
                console.log('tasks was not loaded', error);
                this._setTasksFetchingState(false);
            }
        };

        _createTask = async (event) => {
            try {
                event.preventDefault();
                const { newTaskMessage } = this.state;

                if (!newTaskMessage) {
                    return null;
                }

                this._setTasksFetchingState(true);

                const response = await fetch(url, {
                    method:  'POST',
                    headers: {
                        Authorization:  token,
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify({ message: newTaskMessage }),
                });

                const { data: task, message } = await response.json();

                if (response.status !== 200) {
                    throw new Error(message);
                }

                this.setState(({ tasks }) => ({
                    tasks:           this._sortTasksByGroup([task, ...tasks]),
                    newTaskMessage:  '',
                    isTasksFetching: false,
                }));
            } catch (error) {
                console.log('task was not created', error);
                this._setTasksFetchingState(false);
            }
        };

        _updateTask = async (updatedTask) => {
            try {
                this._setTasksFetchingState(true);

                const response = await fetch(url, {
                    method:  'PUT',
                    headers: {
                        Authorization:  token,
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify([updatedTask]),
                });

                const {
                    data: [taskFromResponse],
                    message: errorMessage,
                } = await response.json();

                if (response.status !== 200) {
                    throw new Error(errorMessage);
                }

                this.setState(({ tasks }) => {
                    const indexToReplace = tasks.indexOf(
                        tasks.find((task) => task.id === updatedTask.id)
                    );

                    const newTasks = [...tasks.filter((task) => task.id !== updatedTask.id)];

                    newTasks.splice(indexToReplace, 0, taskFromResponse);

                    const sortedTasks = this._sortTasksByGroup(newTasks);

                    return {
                        tasks:           sortedTasks,
                        isTasksFetching: false,
                    };
                });
            } catch (error) {
                console.log('task was not created', error);
                this._setTasksFetchingState(false);
            }
        };

        _removeTask = async (id) => {
            try {
                this._setTasksFetchingState(true);

                const response = await fetch(`${url}/${id}`, {
                    method:  'DELETE',
                    headers: {
                        Authorization: token,
                    },
                });

                if (response.status !== 204) {
                    const { message: errorMessage } = await response.json();

                    throw new Error(errorMessage);
                }

                this.setState(({ tasks }) => ({
                    tasks:           tasks.filter((task) => task.id !== id),
                    isTasksFetching: false,
                }));
            } catch (error) {
                console.log('task was not deleted', error);
                this._setTasksFetchingState(false);
            }
        };

        _completeAll = async () => {
            try {
                if (this._getAllCompleted()) {
                    return null;
                }

                const promises = [];

                for (const task of this.state.tasks) {
                    promises.push(
                        fetch(url, {
                            method:  'PUT',
                            headers: {
                                Authorization:  token,
                                'Content-Type': 'application/json',
                            },
                            body: JSON.stringify([{ ...task, completed: true }]),
                        })
                    );
                }

                const responses = await Promise.all(promises);
                const success = responses.every((result) => result.status === 200);

                if (!success) {
                    throw new Error('Posts was not completed');
                }

                this.setState(({ tasks }) => ({
                    tasks: this._sortTasksByGroup(
                        tasks.map((task) => ({ ...task, completed: true }))
                    ),
                    isTasksFetching: false,
                }));
            } catch (error) {
                console.log('✓ all tasks was not completed:', error);
                this._setTasksFetchingState(false);
            }
        };

        _setTasksFetchingState = (state) => {
            this.setState({
                isTasksFetching: state,
            });
        };

        _updateNewTaskMessage = (event) => {
            const newTaskMessage = event.target.value;

            newTaskMessage.length < 50 &&
                this.setState({
                    newTaskMessage,
                });
        };

        _updateTasksFilter = (event) => {
            this.setState({
                tasksFilter: event.target.value.toLowerCase(),
            });
            console.log('✓ this.state:', this.state);
        };

        _sortTasksByGroup = (tasks) => {
            const favorite = tasks.filter((task) => task.favorite && !task.completed);
            const usual = tasks.filter((task) => !task.favorite && !task.completed);
            const completed = this._sortTasksByDate(tasks.filter((task) => task.completed));

            const sortedCompleted = [
                ...completed.sort((task1, task2) => {
                    if (task1.favorite && !task2.favorite) {
                        return -1;
                    } else if (!task1.favorite && task2.favorite) {
                        return 1;
                    }

                    return 0;
                })
            ];

            return [
                ...this._sortTasksByDate(favorite),
                ...this._sortTasksByDate(usual),
                ...sortedCompleted
            ];
        };

        _sortTasksByDate = (tasks) => {
            return tasks.sort((task1, task2) => {
                if (moment(task1.created).unix() < moment(task2.created).unix()) {
                    return 1;
                } else if (moment(task1.created).unix() > moment(task2.created).unix()) {
                    return -1;
                }

                return 0;
            });
        };

        _getAllCompleted = () => this.state.tasks.every((task) => task.completed);

        render () {
            return (
                <Enhanceable
                    _completeAll = { this._completeAll }
                    _createTask = { this._createTask }
                    _getAllCompleted = { this._getAllCompleted }
                    _removeTask = { this._removeTask }
                    _updateNewTaskMessage = { this._updateNewTaskMessage }
                    _updateTask = { this._updateTask }
                    _updateTasksFilter = { this._updateTasksFilter }
                    { ...this.state }
                    { ...this.props }
                />
            );
        }
    }

    WithState.displayName = `withState(${getDisplayName(Enhanceable)})`;

    return WithState;
};
