// Core
import React, { Component } from 'react';
import Move from 'react-flip-move';

// Instruments
import Styles from './styles.m.css';
import Checkbox from 'theme/assets/Checkbox';
import { withState } from 'instruments/withState';

// Components
import Task from 'components/Task';
import Spinner from 'components/Spinner';

@withState
export default class Scheduler extends Component {
    render () {
        const {
            tasks,
            newTaskMessage,
            tasksFilter,
            isTasksFetching,
            _completeAll,
            _createTask,
            _removeTask,
            _updateNewTaskMessage,
            _getAllCompleted,
            _updateTask,
            _updateTasksFilter,
        } = this.props;

        const allCompleted = _getAllCompleted();
        const todoList = tasks
            .filter((task) => task.message.toLowerCase().includes(tasksFilter))
            .map((props) => (
                <Task
                    _removeTask = { _removeTask }
                    _updateTask = { _updateTask }
                    key = { props.id }
                    { ...props }
                />
            ));

        return (
            <section className = { Styles.scheduler }>
                <Spinner isSpinning = { isTasksFetching } />
                <main>
                    <header>
                        <h1>Планировщик задач</h1>
                        <input
                            placeholder = 'Поиск'
                            type = 'search'
                            value = { tasksFilter }
                            onChange = { _updateTasksFilter }
                        />
                    </header>
                    <section>
                        <form onSubmit = { _createTask }>
                            <input
                                className = { Styles.createTask }
                                placeholder = 'Описание моей новой задачи'
                                type = 'text'
                                value = { newTaskMessage }
                                onChange = { _updateNewTaskMessage }
                            />
                            <button>Добавить задачу</button>
                        </form>
                        <div className = { Styles.overlay }>
                            <ul>
                                <Move duration = { 400 } easing = 'ease-in-out'>
                                    {todoList}
                                </Move>
                            </ul>
                        </div>
                    </section>
                    <footer>
                        <Checkbox
                            checked = { allCompleted }
                            color1 = '#363636'
                            color2 = '#fff'
                            onClick = { _completeAll }
                        />
                        <span className = { Styles.completeAllTasks }>Все задачи выполнены</span>
                    </footer>
                </main>
            </section>
        );
    }
}
